// src/components/LeadAnalytics.jsx
import React from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend, Title } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';
import '../App.css'; // Import the main App.css file

// Register Chart.js components
ChartJS.register(ArcElement, Tooltip, Legend, Title);

const LeadAnalytics = ({ leads }) => {
  if (!leads || leads.length === 0) {
    return (
      <div className="analytics-container no-data-message">
        No leads available for analytics yet.
      </div>
    );
  }

  // Aggregate data for the Doughnut chart
  const hotLeads = leads.filter(lead => lead.category === 'hot').length;
  const warmLeads = leads.filter(lead => lead.category === 'warm').length;
  const coldLeads = leads.filter(lead => lead.category === 'cold').length;
  const otherLeads = leads.length - (hotLeads + warmLeads + coldLeads);

  const data = {
    labels: ['Hot Leads', 'Warm Leads', 'Cold Leads', 'Other'],
    datasets: [
      {
        label: 'Number of Leads',
        data: [hotLeads, warmLeads, coldLeads, otherLeads],
        backgroundColor: [
          'rgba(239, 68, 68, 0.8)', // Red for Hot
          'rgba(245, 158, 11, 0.8)', // Orange for Warm
          'rgba(59, 130, 246, 0.8)', // Blue for Cold
          'rgba(156, 163, 175, 0.8)', // Gray for Other
        ],
        borderColor: [
          'rgba(239, 68, 68, 1)',
          'rgba(245, 158, 11, 1)',
          'rgba(59, 130, 246, 1)',
          'rgba(156, 163, 175, 1)',
        ],
        borderWidth: 1,
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'bottom',
        labels: {
          font: {
            size: 14,
          },
          color: '#4a5568', // Dark color for legend labels
        },
      },
      title: {
        display: true,
        text: 'Lead Distribution by Category',
        font: {
          size: 20,
          weight: 'bold',
        },
        color: '#2d3748', // Dark color for chart title
      },
      tooltip: {
        callbacks: {
          label: function(context) {
            let label = context.label || '';
            if (label) {
              label += ': ';
            }
            if (context.parsed !== null) {
              label += context.parsed + ' leads';
            }
            return label;
          }
        }
      }
    },
  };

  return (
    <div className="analytics-container">
      <div className="chart-wrapper">
        <Doughnut data={data} options={options} />
      </div>
    </div>
  );
};

export default LeadAnalytics;