// src/components/CaptureLeadForm.jsx
import React, { useState } from 'react';
import axios from 'axios';
import '../App.css'; // Import the main App.css for styling

const CaptureLeadForm = ({ onLeadAdded }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    budget: '',
    location: ''
  });

  const [message, setMessage] = useState('');
  const [isError, setIsError] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage('');
    setIsError(false);

    try {
      const dataToSend = {
        ...formData,
        budget: formData.budget ? parseFloat(formData.budget) : undefined,
        preferredLocation: formData.location // Map 'location' from form to 'preferredLocation' for API
      };

      const response = await axios.post('http://localhost:8080/api/leads', dataToSend);

      setMessage('✅ Lead captured successfully!');
      setIsError(false);
      setFormData({
        name: '',
        email: '',
        phone: '',
        budget: '',
        location: ''
      });
      if (onLeadAdded) onLeadAdded(); // refresh leads list
    } catch (err) {
      console.error('Error capturing lead:', err);
      if (axios.isAxiosError(err) && err.response) {
        setMessage(err.response.data.message || '❌ Error capturing lead. Please check backend.');
      } else {
        setMessage('❌ Error capturing lead. Please try again.');
      }
      setIsError(true);
    }
  };

  return (
    <div className="lead-form-container">
      <h2>📌 Capture New Lead</h2>
      <form className="lead-form" onSubmit={handleSubmit}>
        <input type="text" name="name" placeholder="Name" value={formData.name} onChange={handleChange} required />
        <input type="email" name="email" placeholder="Email" value={formData.email} onChange={handleChange} required />
        <input type="text" name="phone" placeholder="Phone" value={formData.phone} onChange={handleChange} required />
        <input type="number" name="budget" placeholder="Budget ($)" value={formData.budget} onChange={handleChange} required />
        <input type="text" name="location" placeholder="Preferred Location" value={formData.location} onChange={handleChange} />

        <button type="submit">Capture Lead</button>
        {message && <p className={`message ${isError ? 'error' : 'success'}`}>{message}</p>}
      </form>
    </div>
  );
};

export default CaptureLeadForm;